import React from 'react';
import { Route, Switch } from 'react-router-dom';
import './App.css';
import Homepage from './pages/homepage';

const HatsPage = () => (
  <div><h1>HATS PAGE</h1></div>
)


function App() {
  return (
    <Switch>
      <Route path='/' component={Homepage} />
      <Route path='/shop/hats' component={HatsPage} />
    </Switch>
  );
}

export default App;


